.ins-c-bulk-select .pf-c-dropdown__toggle-check span.pf-c-dropdown__toggle-text {
  display: initial; }

.ins-c-bulk-select .ins-c-bulk-select__selected {
  display: none; }

@media only screen and (max-width: 768px) {
