:root {
  --ins-color--orange: #ec7a08; }

@keyframes spinner {
  to {
    transform: rotate(360deg); } }

.ins-c-spinner {
  display: inline-flex; }
  .ins-c-spinner.ins-m-center {
    display: flex;
    justify-content: center; }
  .ins-c-spinner:before {
    content: '';
    width: 30px;
    width: 1.875rem;
    height: 30px;
    height: 1.875rem;
    border-radius: 50%;
    border: 3px solid var(--pf-global--active-color--200);
    border-top-color: var(--pf-global--active-color--100);
    animation: spinner 1s cubic-bezier(0.4, 0, 0.2, 1) infinite; }
