  .ins-c-rule__report-detail .ins-c-rule__risk-description .ins-c-rule__severity-level-3 .ins-c-risk-of-change__label, .ins-c-rule__report-detail .ins-c-rule__risk-description .ins-c-rule__severity-level-3 .ins-c-risk-of-change__icon, .ins-c-rule__report-detail .ins-c-rule__risk-description .ins-c-rule__severity-level-3 .label {
