:root {
  --ins-color--orange: #ec7a08; }

.ins-c-conditional-filter .pf-c-select__toggle-wrapper .pf-c-select__toggle-text {
  max-width: 150px;
  max-width: 9.375rem; }

.ins-c-conditional-filter .ins-c-conditional-filter__group .pf-c-dropdown__toggle-text {
  width: 102px;
  text-align: left; }
  .ins-c-conditional-filter .ins-c-conditional-filter__group .pf-c-dropdown__toggle-text > svg {
    margin-right: var(--pf-global--spacer--xs); }

.ins-c-conditional-filter .ins-c-conditional-filter__group .ins-c-conditional-filter__no-label .pf-c-dropdown__toggle-text {
  width: auto; }

.ins-c-conditional-filter .pf-l-split__item .pf-c-select__menu-group {
  padding: 0; }
  .ins-c-conditional-filter .pf-l-split__item .pf-c-select__menu-group .pf-c-select__menu-group-title:empty {
    display: none; }

.ins-c-conditional-filter .pf-l-split__item .pf-c-select__option-button {
  padding: 0; }

.ins-c-conditional-filter .pf-l-split__item .pf-c-select__menu-item-icon {
  display: none; }

.ins-c-conditional-filter input.ins-c-conditional-filter[type="text"] {
  padding-right: 35px;
  padding-right: 2.1875rem;
  margin-right: -23px;
  margin-right: -1.4375rem; }
  .ins-c-conditional-filter input.ins-c-conditional-filter[type="text"] + svg {
    position: relative;
    left: -5px;
    left: -0.3125rem;
    top: 2px;
    top: 0.125rem; }

.ins-c-conditional-filter .pf-c-select__toggle-text {
  margin-right: 0; }

.ins-c-conditional-filter .pf-c-select__toggle-badge {
  display: none; }

@media only screen and (max-width: 406px) {
  .ins-c-conditional-filter .pf-c-select__toggle-wrapper .pf-c-select__toggle-text {
    max-width: 100px;
    max-width: 6.25rem; } }

@media only screen and (max-width: 884px) {
  .ins-c-conditional-filter .ins-c-conditional-filter__group .pf-c-dropdown__toggle-text {
    width: auto; }
  .ins-c-conditional-filter .ins-c-conditional-filter__group .ins-c-conditional-filter__value-selector {
    display: none; }
  .ins-c-conditional-filter .ins-c-conditional-filter__group .ins-c-conditional-filter__group {
    width: initial; } }
